import React, { useEffect } from "react"
import ContactUs from "../components/ContactUs/ContactUs"
import { AnimatedCursor } from "../components/utils/AnimatedCursor"
import AOS from "aos"
import "aos/dist/aos.css"
import Layout from "../components/Layout"
export default function Contact() {
  useEffect(() => {
    AOS.init()
  }, [])
  const mouseCoords =
    typeof window !== "undefined" &&
    JSON.parse(window.localStorage.getItem("mouseCoords"))

  return (
    <Layout>
      <ContactUs />
      <AnimatedCursor
        coord={mouseCoords}
        innerSize={16}
        outerSize={16}
        color="255, 255, 255"
        outerAlpha={0.2}
        innerScale={0.8}
        outerScale={4}
        x
        clickables={[
          "a",
          'input[type="text"]',
          'input[type="email"]',
          'input[type="number"]',
          'input[type="submit"]',
          'input[type="image"]',
          "label[for]",
          "select",
          "textarea",
          "button",
          ".link",
          ".swiper-pagination-bullet",
        ]}
      />
    </Layout>
  )
}
